import { mount } from 'enzyme';
import * as React from 'react';
import sinon from 'sinon';
import { App } from 'components/app/App';

const state = {
  toDoData: [
      sinon.stub(),
      sinon.stub(),
      sinon.stub(),
      'asd',
      'active'
  ]
};

describe('src/components/App/App', () => {
    beforeEach(() => {
        sinon.resetHistory();
    });

    it('App component should be rendered', () => {
        const wrapper = mount(<App {...state}/>);

        expect(wrapper.isEmptyRender()).toBeFalsy();
    });

    //createToDoItem
    it('createTodoItem check', () =>{
        //Given
        const label = 'asdasdas';
        const instance = mount<App>(<App />).instance();
        const expected = {
            label,
            important: false,
            done: false,
            id: instance.maxId,
        };

        //When
        const actual = instance.createTodoItem(label);

        //Then
        expect(actual).toEqual(expected);
        expect(instance.maxId).toEqual(104);
    });

    //onAddItem
    it('onAddItem check', () =>{
        //Given
        const text = 'wake up in hell';
        const instance = mount<App>(<App />).instance();
        const expected = {
            label: text,
            important: false,
            done: false,
            id: 104,
        };

        //When
        instance.onAddItem(text);

        expect(instance.state.toDoData[0]).toEqual(expected);
    });

    it('onToggleImportant check',() => {
        //Given
        const wrapper = mount<App>(<App />);
        const instance = wrapper.instance();
        const toDoData = [{
            label: 'hello',
            important: false,
            done: false,
            id: 99,
        }];
        const expected = {
            label: 'hello',
            important: true,
            done: false,
            id: 99,
        };
        wrapper.setState({ toDoData });

        //When
        instance.onToggleImportant(99);

        //Then
        expect(instance.state.toDoData[0]).toEqual(expected);
    });


    it('onToggleDone check',() => {

        //Given
        const instance = mount<App>(<App />).instance();
        const expected = {
            label: 'hello',
            important: false,
            done: false,
            id: 103,
        };

        //When
        instance.onAddItem(expected.label);
        instance.onToggleDone(103);

        //Then
        expect(instance.state.toDoData[0].done).toBeTruthy();
    });

    it('deleteItem check', () => {
        //Given
        const instance = mount<App>(<App />).instance();
        const expected: number = 0;

        //When
        instance.deleteItem(100);
        instance.deleteItem(101);
        instance.deleteItem(102);

        //Then
        expect(expected).toEqual(instance.state.toDoData.length);
    });


    it('onSearchChange check', () => {
        //Given
        const instance = mount<App>(<App />).instance();
        const expected = 'while';

        //When
        instance.onSearchChange(expected);

        //Then
        expect(expected).toEqual(instance.state.term);
    });

    it('onFilter change', () => {
        //Given
        const instance = mount<App>(<App />).instance();
        const expected = 'while';

        //When
        instance.onFilterChange(expected);

        //Then
        expect(expected).toEqual(instance.state.filter);
    });

    it('filter check', () => {
        //Given
        const instance = mount<App>(<App />).instance();

        //When
        instance.onToggleDone(100);
        instance.onToggleDone(101);
        instance.onToggleDone(102);

        //Then
        console.log(1);
    })

    //filter
    //search
    //toggleProperty
});